import React from 'react';
import "./style.css";

export const Modal = (props) => {
  // ,    
  const { open, close } = props;

  return (
    //   openModal  .
    <div className={open ? 'modal_container' : 'modal_container out'}>
      <div className="modal_background" onClick={close}>
          <div class="modal">
            <div>{props.children}</div>
          </div>
        </div>
    </div>
  );
};